import Vue from 'vue'
import Prompt from '../prompt/prompt.vue'

Prompt.newInstance = (properties = {}) => {
  // 1. 
  const PromptConstructor = Vue.extend(Prompt)

  // 2.  props data + v-model
  const instance = new PromptConstructor({
    propsData: {
      // 
      ...properties,

      // 
      visible: false,
      text: '',
      placeholderText: '',
      width: '70%',
      title: '',
      okText: '',
      cancleText: '',
      loading: false,
      showCancle: true,
      spec: '',
      message: '',
      validator: null,
      onOk: () => {},
      onCancle: () => {}
    }
  })

  // 3.  DOM
  instance.$mount()
  document.body.appendChild(instance.$el)

  // 4. 
  const destroy = () => {
    instance.visible = false
    setTimeout(() => {
      instance.$destroy()
      instance.$el && document.body.removeChild(instance.$el)
      instance.onRemove && instance.onRemove()
    }, 300)
  }

  return {
    /**
     *  Prompt
     */
    show (props = {}) {
      Object.keys(props).forEach(key => {
        instance[key] = props[key]
      })
      instance.visible = true
    },

    /**
     *  Prompt
     */
    remove () {
      instance.visible = false
      instance.buttonLoading = false
      destroy()
    },

    component: instance
  }
}

export default Prompt
