<template>
  <div
    :class="containerClasses"
    :style="{height:height}"
    @scroll="onScroll"
  >
    <div
      v-if="refresh"
      :class="topClasses"
      :style="{height:translateY+'px'}"
    >
      <div :class="statusClass">
        <slot name="top">
          <span class="spinner"><spinner
            v-if="upStatus == 'loading'"
            size="15"
            type="snake"
          /></span><span
            class="dpzvc-loadmore-text"
          >{{ upText }}</span>
        </slot>
      </div>
    </div>
    <div :class="contentClasses">
      <slot />
    </div>

    <div
      v-if="loadMore"
      :class="bottomClasses"
    >
      <div :class="statusClass">
        <slot name="bottom">
          <span class="spinner"><spinner
            v-if="downStatus == 'loading'"
            size="15"
            type="snake"
          /></span><span class="dpzvc-loadmore-text">{{ downText }}</span>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
import Spinner from '../spinner'
const prefixCls = 'dpzvc-loadmore'
export default {
  name: 'DpzVcLoadmore',
  components: {
    Spinner
  },
  props: {
    height: {
      type: [Number, String],
      default: '100%'
    },
    refresh: Function,
    upLoadingText: {
      type: String,
      default: '...'
    },
    upDistance: {
      type: Number,
      default: 70
    },
    upPullText: {
      type: String,
      default: ' '
    },
    upDropText: {
      type: String,
      default: ' '
    },
    maxDistance: {
      type: Number,
      default: 0
    },
    speed: {
      type: Number,
      default: 3
    },
    downEndText: {
      type: String,
      default: ''
    },
    downDropText: {
      type: String,
      default: ' '
    },
    downLoadingText: {
      type: String,
      default: '...'
    },
    downDistance: {
      type: Number,
      default: 50
    },
    loadMore: Function,
    hasMore: {
      type: Boolean,
      default: true
    },
    styles: {
      type: Object,
      default () {
        return {}
      }
    },
    auto: {
      type: Boolean,
      default: true
    },
    autoFill: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      translateY: 0,
      startTranslateY: 0,
      currentY: 0,
      startY: 0,
      upStatus: '',
      downStatus: '',
      direction: '',
      upText: '',
      downText: '',
      down: false,
      drag: false,
      more: this.hasMore
    }
  },
  computed: {
    containerClasses () {
      return [
        `${prefixCls}`
      ]
    },
    topClasses () {
      return [
        `${prefixCls}-top`,
        {
          [`${prefixCls}-drag`]: !this.drag
        }
      ]
    },
    bottomClasses () {
      return [
        `${prefixCls}-bottom`
      ]
    },
    contentClasses () {
      return [
        `${prefixCls}-content`
      ]
    },
    statusClass () {
      return [
        `${prefixCls}-status`
      ]
    }
  },
  watch: {
    upStatus (val) {
      switch (val) {
      case 'pull':
        this.upText = this.upPullText
        break
      case 'drop':
        this.upText = this.upDropText
        break
      case 'loading':
        this.upText = this.upLoadingText
      }
      this.$emit('on-change-up-status', val)
    },
    downStatus (val) {
      switch (val) {
      case 'end':
        this.downText = this.downEndText
        break
      case 'drop':
        this.downText = this.downDropText
        break
      case 'loading':
        this.downText = this.downLoadingText
        break
      default:
        this.downText = ''
        break
      }
      this.$emit('on-change-down-status', val)
    },
    hasMore (val) {
      this.more = val
      if (!val) this.downStatus = 'end'
    }
  },
  mounted () {
    if (this.auto && this.refresh && typeof this.refresh === 'function') {
      this.translateY = 40
      this.drag = false
      this.direction = 'down'
      this.upStatus = 'loading'
      this.refresh()
    }
    this.bindEvents()
  },
  beforeDestroy () {
    this.unbindEvents()
  },
  methods: {
    onLoadOff () {
      this.translateY = 0
      this.upStatus = ''
      this.downStatus = ''
      if (!this.more) this.downStatus = 'end'
      setTimeout(() => {
        if (this.more && this.isBottom()) {
          this.direction = 'up'
          this.downStatus = 'loading'
          this.loadMore()
        }
      }, 1000)
    },
    isBottom () {
      if (this.$el.scrollHeight > this.$el.offsetHeight) return false
      return true
    },
    onScroll (e) {
      e.preventDefault()
      if (this.downStatus === 'loading') return
      const scrollTop = this.$el.scrollTop
      if (this.loadMore && typeof this.loadMore === 'function') {
        const absY = this.$el.scrollHeight - (this.$el.offsetHeight + scrollTop)
        if (absY > this.downDistance && this.downStatus === 'drop') {
          this.downStatus = ''
        } else if (absY <= this.downDistance && absY > this.downDistance && this.more) {
          this.downStatus = 'drop'
        } else if (absY <= this.downDistance) {
          this.downStatus = 'loading'
          if (this.more) {
            this.downStatus = 'loading'
            this.loadMore()
          } else {
            this.downStatus = 'end'
            const that = this
            setTimeout(() => {
              that.onLoadOff()
            }, 1000)
          }
        }
      }
    },
    onTouchStart (e) {
      if (this.upStatus === 'loading') return
      this.startY = e.touches[0].clientY
      this.startTranslateY = this.translateY
      this.down = false
    },
    onTouchMove (e) {
      const pos = this.$el.getBoundingClientRect()
      if (this.upStatus === 'loading' || (this.startY < pos.top && this.startY > pos.down)) return
      this.currentY = e.touches[0].clientY
      const distance = (this.currentY - this.startY) / this.speed
      const scrollTop = this.$el.scrollTop
      this.direction = distance > 0 ? 'down' : 'up'
      if (this.currentY >= this.startY && typeof this.refresh === 'function' && scrollTop === 0 && this.direction === 'down') {
        event.preventDefault()
        event.stopPropagation()
        if (this.maxDistance > 0) {
          this.translateY = distance <= this.maxDistance ? distance - scrollTop : this.translateY
        } else {
          this.translateY = distance - scrollTop
        }
        if (this.translateY < 0) {
          this.translateY = 0
        }
        this.upStatus = this.translateY >= this.upDistance ? 'drop' : 'pull'
        this.drag = true
        this.down = this.down || this.isBottom()
      }
    },
    onTouchEnd (e) {
      if (this.upStatus === 'loading') return
      if (this.direction === 'down' && this.$el.scrollTop <= 0 && this.translateY >= 0) {
        if (this.upStatus === 'drop') {
          this.translateY = 40
          this.upStatus = 'loading'
          this.refresh()
        } else {
          this.translateY = 0
        }
      }
      this.direction = ''
      this.drag = false
    },
    bindEvents () {
      this.$el.addEventListener('touchstart', this.onTouchStart, false)
      this.$el.addEventListener('touchmove', this.onTouchMove, false)
      this.$el.addEventListener('touchend', this.onTouchEnd, false)
    },
    unbindEvents () {
      this.$el.removeEventListener('touchstart', this.onTouchStart, false)
      this.$el.removeEventListener('touchmove', this.onTouchMove, false)
      this.$el.removeEventListener('touchend', this.onTouchEnd, false)
    }
  }
}
</script>
